.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
*{
  box-sizing: border-box;
}
@font-face {
  font-family: 'S-CoreDream-3Light';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_six@1.2/S-CoreDream-3Light.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'Giants-Inline';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2307-1@1.1/Giants-Inline.woff2') format('woff2');
  font-weight: normal;
  font-style: normal;
}
:root {
  --gray100: #F2F2F2;
  --gray200: #D9D9D9;
  --gray300: #808080;
  --gray400: #333333;
  --gray500: #262626;
  --gray600: #1A1A1A;
  --gray700: #0D0D0D;
  --purple: #8284FA;
  --purpledark: #5E60CE;
  --blue: #4EA8DE;
  --bluedark: #1E6F9F;
  --danger: #E25858;
  --mw: 800px;
}
body{
  background-color: var(--gray600);
  color: var(--gray100);
  font-family: 'S-CoreDream-3Light';
}
a{
  text-decoration: none;
  color: inherit;
}
header{
  height: 200px;
  background-color: var(--gray700);
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 3rem;
}
header h1{
  font-size: 2rem;
  font-family: 'Giants-Inline';
}
header h1::before{
  content: "\f135";
  font-family: "Font Awesome 6 Free";
  font-weight: 900;
  color: var(--purpledark);
  display: inline-block;
  transform: rotate(-45deg) translate(-4px, -4px);
  
}

header span:first-of-type{
  color: var(--blue);
}
header span:last-of-type{
  color: var(--bluedark);
}
.textInput{
  display: flex;
  gap: 1rem;
  position: relative;
  top: -2rem;
}
.textInput input,
.textInput button {
  border-radius: 0.5rem;
  padding: 1rem 2rem;
  border: none;
}
.textInput input {
  background-color: var(--gray500);
  color: var(--gray100);
  flex-grow: 1;
}
.textInput input:focus{
  background-color: var(--gray400);
}
.textInput button {
  background-color: var(--blue);
  color: var(--gray100);
}
.textInput button:hover,
.textInput button:focus{
  background-color: var(--bluedark);
}

@media (max-width:500px){
  .textInput{
      flex-direction: column;
  }
}
.total{
  text-align: right;
  font-size: 0.75rem;
  color: var(--purpledark);
  margin-bottom: 1rem;
}
.total span{
  display: inline-block;
  color: var(--gray100);
  background-color: var(--gray300);
  padding: 0.2em 0.5em;
  border-radius: 50%;
}

.list li {
  display: flex;
  justify-content: space-between;
  background-color: var(--gray500);
  margin-bottom: 1rem;
  border: 1px solid var(--gray400);
  border-radius: 0.5rem;
}
.list li p {
  white-space: nowrap;
  overflow:hidden;
  text-overflow: ellipsis;
  padding: 1rem 2rem;
}
.list li i {
  display: inline-block;
  padding: 1rem;
  transition: 0.5s;
}
.list li i:hover{
  color: var(--danger);
}
.popup {
  /* background-color: var(--gray100); */
  background-color: rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(5px);
  padding: 3rem;
  color: var(--gray700);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}